<template>
  <div class="content">
    <div class="left">
      <div class="container">
        <div class="post">
          <a class="vote">
            <span class="iconfont icon-up" @click="vote(post.id, '1')"></span>
          </a>
          <span class="text">{{ post.vote_num }}</span>
          <a class="vote">
            <span class="iconfont icon-down" @click="vote(post.id, '-1')"></span>
          </a>
        </div>
        <div class="l-container">
          <h4 class="con-title">{{ post.title }}</h4>
          <div class="con-info">{{ post.content }}</div>
          <div class="user-btn">
            <span class="btn-item">
              <i class="iconfont icon-comment"></i> Comment
            </span>
          </div>
        </div>
      </div>
    </div>
    <div class="right">
      <div class="topic-info">
        <h5 class="t-header"></h5>
        <div class="t-info">
          <a class="avatar"></a>
          <span class="topic-name">b/{{ post.community_name }}</span>
        </div>
        <p class="t-desc">风铃草论坛，发现你喜欢</p>
        <ul class="t-num">
          <li class="t-num-item">
            <p class="number">1 Members</p>
            <span class="unit">访问</span>
          </li>
          <li class="t-num-item">
            <p class="number">1 Members</p>
            <span class="unit">喜欢</span>
          </li>
        </ul>
        <div class="date">{{ getFormattedDate(post.created_at) }}</div> <!-- 更新的行，使用 getFormattedDate 方法获取格式化的创建日期 -->
        <button class="topic-btn">JOIN</button>
      </div>
    </div>
  </div>
</template>


<script>
export default {
  name: "Content",
  data(){
    return {
      post:{},
    }
  },
  methods:{
    getPostDetail() {
      this.$axios({
        method: "get",
        url: "/post/"+ this.$route.params.id,
      })
        .then(response => {
          console.log(1, response.data);
          if (response.code === 1000) {
            this.post = response.data;
          } else {
            console.log(response.msg);
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    vote(post_id, direction) {
      this.$axios({
        method: "post",
        url: "/vote",
        headers: {
          Authorization: `Bearer ${this.getJWTToken()}`
        },
        data: {
          post_id: post_id,
          direction: direction
        }
      })
          .then(response => {
            if (response.data.code === 0) {
              console.log("Vote success");
            } else {
              console.log(response.data.msg);
            }
          })
          .catch(error => {
            console.log(error);
          });
    }
  },
  mounted: function() {
    this.getPostDetail();
  }
};
</script>

