      <h2 class="form-title">注册</h2>
      <div class="form-group margin-adjust">
        <label for="name">用户名</label>
        <input type="text" class="form-control" name="name" id="name" placeholder="用户名" v-model="username" required/>
      </div>
      <div class="form-group margin-adjust">
        <label for="phone">手机号</label>
        <div class="verification-group">
          <input type="text" class="form-control" v-model="phone" name="phone" id="phone" placeholder="手机号" required/>
        </div>
      </div>
      <div class="form-group margin-adjust">
        <label for="pass">密码</label>
        <input type="password" class="form-control" name="pass" id="pass" placeholder="密码" v-model="password" required/>
      </div>
      <div class="form-group margin-adjust">
        <label for="re_pass">确认密码</label>
        <input type="password" class="form-control" name="re_pass" id="re_pass" placeholder="确认密码"  v-model="re_password"/>
      </div>
      <div class="form-btn">
        <button  class="btn btn-info" type="button" @click="submit"> 注册</button>
      </div>
