<script>
import Swal from "sweetalert2";

export default {
  name: "SignUp",
  data() {
    return {
      username: "",
      phone:"",
      password: "",
      re_password: "",
      submitted: false
    };
  },
  computed: {
  },
  created() {
  },
  methods: {
    submit() {
      // 验证用户名不能为空
      if (!this.username) {
        Swal.fire('错误', '用户名不能为空', 'error');
        return;
      }

      // 验证手机号不能为空
      if (!this.phone) {
        Swal.fire('错误', '手机号不能为空', 'error');
        return;
      }

      // 验证密码不能为空
      if (!this.password) {
        Swal.fire('错误', '密码不能为空', 'error');
        return;
      }

      // 验证密码长度不能小于八位
      if (this.password.length < 8) {
        Swal.fire('错误', '密码长度不能小于八位', 'error');
        return;
      }

      if(this.password !== this.re_password){
        Swal.fire('错误', '两次密码不一致', 'error');
        return;
      }
      // 继续发送请求进行注册
      this.$axios({
        method: 'post',
        url:'/signup',
        data: JSON.stringify({
          username: this.username,
          phone: this.phone,
          password: this.password,
          re_password: this.re_password
        })
      }).then((res)=>{
        console.log(res.data);
        if (res.code == 1000) {
          console.log('signup success');
          this.$router.push({ name: "Login" });
        }else{
          console.log(res.msg);
        }
      }).catch((error) => {
        console.log(error);
      });
    },
    getJWTToken() {
      // Implement the logic to retrieve your JWT token here
      return 'your-jwt-token';
    }
  }
};
</script>
