<script>
export default {
  name: "Login",
  data() {
    return {
      loginMethod: 'username',
      username: "",
      password: "",
      phone: "",
      verificationCode: "",
      submitted: false
    };
  },
  methods: {
    setLoginMethod(method) {
      this.loginMethod = method;
    },
    //表单提交
    submit() {
      if (this.loginMethod === 'username') {
        //用户名登录
        this.$axios({
          method: 'post',
          url: '/login',

          headers: {
            Authorization: `Bearer ${this.getJWTToken()}`
          },
          data: JSON.stringify({
            username: this.username,
            password: this.password
          })
        }).then((res) => {
          console.log(res.data)
          if (res.code === 1000) {
            // const defaultLoginSuccessMessage = '登录成功！';
            // this.$message.success(defaultLoginSuccessMessage);
            localStorage.setItem("loginResult", JSON.stringify(res.data));
            this.$store.commit("login", res.data);
            this.$router.push({path: this.redirect || '/'});
          } else {
            console.log(res.message);
          }
        }).catch((error) => {
          // const defaultLoginFailureMessage = '登录失败，请重试！';
          console.log(error);
          // this.$message.error(defaultLoginFailureMessage);
        });
      } else if (this.loginMethod === 'verification') {
        this.$axios({
          method: 'post',
          url: '/login',
          headers: {
            Authorization: `Bearer ${this.getJWTToken()}`
          },
          data: JSON.stringify({
            phone: this.phone,
            verificationCode: this.verificationCode
          })
        }).then((res) => {
          if (res.data.code === 1000) {
            localStorage.setItem("loginResult", JSON.stringify(res.data));
            this.$store.commit("login", res.data);
            console.log(2)
            this.$router.push({path: this.redirect || '/'});
            console.log(1)
          } else {
            console.log(res.data.msg);
          }
        }).catch((error) => {
          console.log(error);
        });
      }
    },
    sendVerificationCode() {
      this.$axios({
        method: 'post',
        url: '/loginSMS',
        headers: {
          Authorization: `Bearer ${this.getJWTToken()}`
        },
        data: JSON.stringify({
          phone: this.phone
        })
      }).then((res) => {
        console.log(res.data);
      }).catch((error) => {
        console.log(error);
      })
    },
    getJWTToken() {
      // Implement the logic to retrieve your JWT token here
      return 'your-jwt-token';
    }
  }
};
</script>


