<template>
  <div class="satic-area">
    <div class="dynamic-area1"></div>
    <div class="dynamic-area2"></div>
    <div class="content">
      <div class="sidebar-container">
        <div class="left-sidebar">
          <h1>热门网站</h1>
          <ul>
            <li><a href="https://google.com" target="_blank">谷歌主页</a></li>
            <li><a href="https://github.com/" target="_blank">github</a></li>
            <li><a href="https://poe.com/" target="_blank">poe:AI模型</a></li>
            <li><a href="https://leetcode.cn/problemset/" target="_blank">力扣</a></li>
            <li><a href="https://chat.openai.com/" target="_blank">chatGPT</a></li>
          </ul>
        </div>
        <div class="right-sidebar">
          <h1>学习资料</h1>
          <ul>
            <li><a href="https://hit-alibaba.github.io/interview/basic/network/TCP.html">计网面经</a></li>
            <li><a href="https://wx.zsxq.com/dweb2/index/group/51122858222824">知识星球</a></li>
            <li><a href="https://hellogithub.com/">HelloGitHub:开源项目分享</a></li>
            <li><a href="https://pdai.tech/md/algorithm/alg-domain-id-snowflake.html">Java知识体系</a></li>
          </ul>
        </div>
      </div>
      <div class="left ">
        <div class="c-l-header">
          <div class="new btn-iconfont" :class="{ active: timeOrder }" @click="selectOrder('time')">
            <i class="iconfont icon-polygonred"></i>最新发布
          </div>
          <div class="top btn-iconfont" :class="{ active: scoreOrder }" @click="selectOrder('score')">
            <i class="iconfont icon-top"></i>热度最高
          </div>
          <button class="btn-publish" @click="goPublish">发布新帖</button>
        </div>
        <ul class="c-l-list">
          <li class="c-l-item" v-for="post in postList" :key="post.id">
            <div class="post">
              <a class="vote">
                <span class="iconfont icon-up" @click="vote(post.id, '1')"></span>
              </a>
              <span class="text">{{ post.vote_num }}</span>
              <a class="vote">
                <span class="iconfont icon-down" @click="vote(post.id, '-1')"></span>
              </a>
            </div>
            <div class="l-container" @click="goDetail(post.id)">
              <h4 class="con-title">{{ post.title }}</h4>
              <div class="con-memo">
                <p>{{ post.content.slice(0, 40) }}...</p>
              </div>
            </div>
          </li>
        </ul>
      </div>
      <div class="right">
        <div class="communities">
          <h2 class="r-c-title">今日热门社区</h2>
          <ul class="r-c-content">
            <li class="r-c-item" v-for="channel in hotChannels" :key="channel.id">
              <span class="index">{{ channel.rank }}</span>
              <div class="icon"></div>
              <span class="channel-name">{{ channel.name }}</span>
            </li>
          </ul>
          <button class="view-all">查看所有</button>
        </div>
        <div class="r-trending">
          <h2 class="r-t-title">所有社区</h2>
          <ul class="rank">
            <li class="r-t-cell" v-for="channel in trendingChannels" :key="channel.id">
              <div class="r-t-cell-info">
                <div class="avatar"></div>
                <div class="info">
                  <span class="info-title">{{ channel.name }}</span>
                  <p class="info-num">{{ channel.members }} members</p>
                </div>
              </div>
              <button class="join-btn">JOIN</button>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Home",
  components: {},
  data() {
    return {
      order: "time",
      page: 1,
      postList: [],
      hotChannels: [
        {id: 1, rank: 1, name: "GO"},
        {id: 2, rank: 2, name: "leetCode"}
      ],
      trendingChannels: [
        {id: 1, name: "GO", members: 1000},
        {id: 2, name: "leetCode", members: 800}
      ]
    };
  },
  methods: {
    getJWTToken() {
      return localStorage.getItem('jwtToken');
    },
    selectOrder(order) {
      this.order = order;
      this.getPostList();
    },
    goPublish() {
      this.$router.push({name: "Publish"});
    },
    goDetail(id) {
      this.$router.push({name: "Content", params: {id: id}});
    },
    // loadData() {
    //   this.getPostList();
    // },
    getPostList() {
      this.$axios({
        method: "get",
        url: "/posts2",
        headers: {
          Authorization: `Bearer ${this.getJWTToken()}`
        },
        params: {
          page: this.page,
          order: this.order
        }
      })
          .then(response => {
            console.log(response.data, 222);
            if (response.code === 1000) {
              this.postList = response.data;
            } else {
              console.log(response.msg);
            }
          })
          .catch(error => {
            console.log(error);
          });
    },
    vote(post_id, direction) {
      this.$axios({
        method: "post",
        url: "/vote",
        headers: {
          Authorization: `Bearer ${this.getJWTToken()}`
        },
        data: {
          post_id: post_id,
          direction: direction
        }
      })
          .then(response => {
            if (response.data.code === 0) {
              console.log("Vote success");
            } else {
              console.log(response.data.msg);
            }
          })
          .catch(error => {
            console.log(error);
          });
    }
  },
  mounted: function () {
    this.getPostList();
  },
  computed: {
    timeOrder() {
      return this.order === "time";
    },
    scoreOrder() {
      return this.order === "score";
    }
  }
};
</script>


<style scoped lang="less">
