  position: absolute;
  width: 100%;
  height: 1000px;
  top: 0;
  left: 0;
  background: url(~@/assets/images/poster-drop-animate2.png) repeat-x 0px 0px;
  background-size: cover;
  animation: posterDrop1 6000s linear infinite;
}


@keyframes posterDrop1 {
  from {
    background-position: 0 0;
  }
  to {
    background-position: 4000% 0;
  }
}

.dynamic-area2 {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: url(~@/assets/images/poster-drop-animate1.png) repeat-x 0px 0px;
  background-size: cover;
  animation: posterDrop2 8000s linear infinite;
}


.content {
  max-width: 1500px;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 48px auto 0;
  padding: 20px 24px;

  .left {
    width: 150px;
    padding-bottom: 10px;
    flex-grow: 1;
    z-index: 10;

    .c-l-title {
      font-size: 14px;
      font-weight: 500;
      line-height: 18px;
      color: #1a1a1b;
      text-transform: unset;
      padding-bottom: 10px;
    }

