<template>
  <div class="container">
    <div class="user-info">
      <img src="../assets/images/logo_new.png" alt="用户头像" class="user-avatar">
      <h2 class="user-nickname">用户昵称</h2>
      <br>
    </div>
    <div class="tabs">
      <button class="tab-button" :class="{ active: activeTab === 'profile' }" @click="changeTab('profile')">个人资料</button>
      <button class="tab-button" :class="{ active: activeTab === 'posts' }" @click="changeTab('posts')">我的发布</button>
      <button class="tab-button" :class="{ active: activeTab === 'favorites' }" @click="changeTab('favorites')">我的收藏</button>
    </div>
    <div class="tab-content">
      <div class="tab-pane" :class="{ active: activeTab === 'profile' }" id="profile">
        <h3>个人资料</h3>
        <div class="selftext">
          <hr>
          昵称：{{ nickname }} <hr>
          邮箱：{{ email }} <hr>
          电话：{{ phoneNumber }} <hr>
          个性签名：{{ signature }}
          <hr>
          <button class="btn btn-primary btn-lg" @click="openModal">修改</button>
          <div class="modal" v-if="showModal">
             <div class="modal-content">
                <h3>修改个人资料</h3>
                <form @submit.prevent="saveChanges">
                  <label for="nickname">昵称</label>
                  <input type="text" id="nickname" v-model="editedNickname">

                  <label for="email">邮箱</label>
                  <input type="email" id="email" v-model="editedEmail">

                  <label for="phoneNumber">电话</label>
                  <input type="text" id="phoneNumber" v-model="editedPhoneNumber">

                  <label for="signature">个性签名</label>
                  <textarea id="signature" v-model="editedSignature"></textarea>

                  <button type="submit" class="btn btn-primary btn-lg">保存</button>
                  <button type="button" class="btn btn-secondary btn-lg" @click="closeModal">取消</button>
                </form>
            </div>
          </div>
        </div>
      </div>
      <div class="tab-pane" :class="{ active: activeTab === 'posts' }" id="posts">
        <h3>我的发布</h3>
        <!-- 这里可以放置用户发布的内容 -->
      </div>
      <div class="tab-pane" :class="{ active: activeTab === 'favorites' }" id="favorites">
        <h3>我的收藏</h3>
        <!-- 这里可以放置用户收藏的内容 -->
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      activeTab: 'profile',
      nickname: '用户昵称',
      email: '1234567890@qq.com',
      phoneNumber: '12312341234',
      signature: '乐急孝典绷',
      showModal: false,
      editedNickname: '',
      editedEmail: '',
      editedPhoneNumber: '',
      editedSignature: ''
    };
  },
  methods: {
    changeTab(tab) {
      this.activeTab = tab;
    },
    openModal() {
      // 打开模态框时，将当前用户资料赋值给编辑字段
      this.editedNickname = this.nickname;
      this.editedEmail = this.email;
      this.editedPhoneNumber = this.phoneNumber;
      this.editedSignature = this.signature;
      this.showModal = true;
    },
    saveChanges() {
      // 保存修改后的用户资料
      this.nickname = this.editedNickname;
      this.email = this.editedEmail;
      this.phoneNumber = this.editedPhoneNumber;
      this.signature = this.editedSignature;
      this.closeModal();
    },
    closeModal() {
      this.showModal = false;
    }
  }
};
</script>
<style>
body {
    font-family: Arial, sans-serif;
    background-color: #f4f4f4;
    margin: 0;
    padding: 0;
}

.container {
    width: 80%;
    max-width: 1200px;
    margin: 0 auto;
    background-color: #fff;
    padding: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    margin-top: 50px;
}

.user-info {
    text-align: center;
}

.user-avatar {
    width: 100px;
    height: 100px;
    border-radius: 50%;
}

.user-nickname {
    margin: 10px 0;
}

.tabs {
    display: flex;
    justify-content: space-around;
    margin-top: 20px;
}

.tab-button {
    background-color: #f0f0f0;
    border: none;
    padding: 15px 30px;
    cursor: pointer;
    border-radius: 5px 5px 0 0;
}

.tab-button.active {
    background-color: #6da6e6;
    /* border-bottom: 2px solid #007BFF; */
}

.tab-content {
    border-top: 2px solid #6da6e6;
    padding: 40px;
}

.tab-pane {
    display: none;
}

.tab-pane.active {
    display: block;
}

form {
    display: flex;
    flex-direction: column;
    max-width: 400px;
    margin: 0 auto;
}

label {
    margin: 10px 0 5px;
}

input {
    padding: 10px;
    margin-bottom: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

button[type="submit"] {
    padding: 10px;
    background-color: #6da6e6;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

button[type="submit"]:hover {
    background-color: #6da6e6;
}

.selftext {
    font-size: 18px;
    text-align: left;
    line-height: 2;
}
</style>